"use strict";

// 加载其他首页数据
// 配置代理
function datalist() {
  function list() {
    var options = {
      // http://303z9z3029.qicp.vip/
      url: "http://303z9z3029.qicp.vip/datalist",
      success: reload,
      dataType: "json"
    };
    ajax(options);
  }

  function reload(res) {
    var i = 0;
    var myarr = new Array();
    var data = res.data.home_floors;
    var flage = true; // console.log(data)

    var html = "";
    var j = 0; // console.log(data) 

    data.forEach(function (item1, index1) {
      // console.log(item1, index1)
      var title = item1.title;
      var cont = item1.tabs[0].tab_items.length; // console.log(cont)
      //渲染外层结构
      // console.log($("#jakeby").eq(1))

      var img_index = "#" + index1 + " .advertise-img";
      item1.tabs[0].tab_items.forEach(function (item, index) {
        //获取到数据，根据不同区域进行渲染  可以进行条件判断，对位置进行定位
        if (index === 0) {
          $(" <div class=\"common-normal-box\" id=\"".concat(index1, "\" >\n          <div class=\"header\">\n            <h5>").concat(title, "</h5>\n          </div>\n          <div class=\"prduct-list\" id=\"prduct-list\">\n            <div class=\"advertise-img\" >\n              <img\n              data-original=\"").concat(item.image, "\"\n                alt=\"\"\n              />\n              </div>\n              </div>\n              </div>\n            ")).insertBefore(".app-wrap #footer_container");
        } else if (flage && index < cont) {
          // console.log(i)
          // console.log(img_index)
          $(" <div class=\"list-duct\">\n                  <figure class=\"item-cover\">\n                    <img\n                    data-original=\"".concat(item.spu.sku_info[0].ali_image, "\"\n                      lazy=\"loaded\"\n                    />\n                  </figure>\n                  <article class=\"artivle\">\n                    <h3>").concat(item.spu.name, "</h3>\n                    <h5 class=\"txt-product-title\">\n                    ").concat(item.spu.sku_info[0].sub_title, "\n                    </h5>\n                  </article>\n                  <aside class=\"item-attr-colors\"></aside>\n                  <!-- \u4EF7\u683C -->\n                  <article class=\"item-price\">\n                    <span>\uFFE5").concat(item.spu.price, "</span>\n                    <!-- <span class=\"orignal-price\">\uFFE5").concat(item.spu.price, "</span> -->\n                  </article>\n                </div>")).insertAfter(img_index); // $("divdata_index="${index1}])
        }
      });
    }); // console.log(myarr)
    // _("#dyduct-list").innerHTML = html;

    $(".common-normal-box img").lazyload({
      placeholder: "data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAYAAAAfFcSJAAAAAXNSR0IArs4c6QAAAARnQU1BAACxjwv8YQUAAAAJcEhZcwAADsQAAA7EAZUrDhsAAAANSURBVBhXYzh8+PB/AAffA0nNPuCLAAAAAElFTkSuQmCC",
      effect: "fadeIn"
    });
  }

  list();
}

datalist();